/**
* Media queries
**/
.quantity-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}
.quantity-wrapper .quantity {
  display: flex;
  text-align: center;
  gap: 0 15px;
  width: 100%;
}
.quantity-wrapper .quantity.out {
  opacity: 0.4;
}
.quantity-wrapper .quantity.out,
.quantity-wrapper .quantity.out * {
  cursor: not-allowed;
}
.quantity-wrapper .quantity.out input {
  pointer-events: none;
}
.quantity-wrapper .quantity input {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-2);
  color: var(--clr-smoke-2);
  line-height: 30px;
  max-width: 100px;
  text-align: center;
  border: none;
}
.quantity-wrapper .quantity button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.quantity-wrapper .stock {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.quantity-wrapper .stock p {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.quantity-wrapper .stock p:nth-of-type(2) {
  font-family: var(--font-family-2);
  opacity: 0.3;
}
.quantity-wrapper .stock p.out {
  opacity: 0.5;
}
.quantity-wrapper .stock .sep {
  width: 1px;
  height: 32px;
  background-color: var(--clr-smoke-2);
  opacity: 0.3;
}