/**
* Media queries
**/
.main-product-wrapper .product .swiper {
  margin: 0;
}
.main-product-wrapper .product .swiper-group-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 25px;
  overflow: hidden;
  height: 826px;
}
.main-product-wrapper .product .product-swiper {
  height: 100%;
  cursor: pointer;
}
.main-product-wrapper .product .product-swiper__slide {
  width: 100%;
}
.main-product-wrapper .product .product-swiper__slide img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}
.main-product-wrapper .product .product-swiper--main {
  max-width: none;
}
.main-product-wrapper .product .product-swiper--main img {
  height: 826px;
}
.main-product-wrapper .product .product-swiper--support {
  min-width: 237px;
}
.main-product-wrapper .product .product-swiper--support .swiper-slide {
  max-height: 262px;
  opacity: 0.3;
}
.main-product-wrapper .product .product-swiper--support .swiper-slide.swiper-slide-thumb-active {
  opacity: 1;
}
.main-product-wrapper .product .product-swiper--support img {
  width: 100%;
  height: 262px;
}
.main-product-wrapper .product h3 {
  letter-spacing: 4.3px;
  text-transform: uppercase;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product h3 {
    margin-bottom: 19px;
  }
}
.main-product-wrapper .product h2 {
  margin-bottom: 10px;
  text-transform: capitalize;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product h2 {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-h2-mob-2-1);
    color: var(--clr-smoke-2);
    line-height: 44px;
  }
}
.main-product-wrapper .product p.price {
  margin-bottom: 10px;
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-red);
  line-height: 30px;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product p.price {
    margin-bottom: 25px;
  }
}
.main-product-wrapper .product .desc p {
  margin-bottom: 20px;
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
  letter-spacing: 0px;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product .desc p {
    margin-bottom: 51px;
  }
}
.main-product-wrapper .varient-options {
  display: flex;
  justify-content: space-between;
  gap: 0 15px;
}
.main-product-wrapper .varient-options .wrapper {
  width: 100%;
}
.main-product-wrapper .varient-options__size, .main-product-wrapper .varient-options__gender {
  display: grid;
  gap: 10px;
  width: 100%;
  max-width: 300px;
  margin: 10px 0;
}
.main-product-wrapper .varient-options__size {
  grid-template-columns: 1fr 1fr 1fr;
}
.main-product-wrapper .varient-options__gender {
  grid-template-columns: 1fr;
}
.main-product-wrapper .varient-options p.option-title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.main-product-wrapper .varient-options .button.button--outline {
  padding: 5px 0px;
  width: auto;
  border: 2px solid rgba(48, 48, 48, 0.2);
}
.main-product-wrapper .varient-options .button.button--outline span {
  color: var(--clr-smoke-2);
}
.main-product-wrapper .varient-options .button.button--outline:hover span, .main-product-wrapper .varient-options .button.button--outline.active span {
  color: var(--clr-white);
}
.main-product-wrapper .varient-options .button.button--outline:after {
  background: var(--clr-smoke-2);
}
.main-product-wrapper .varient-options .button.button--outline.active:after {
  transform: translate(0);
}